const canvasSketch = require('canvas-sketch');
const random = require('canvas-sketch-util/random');
const math = require('canvas-sketch-util/math');
const Tweakpane = require('tweakpane');

const settings = {
  dimensions: [ 1920, 1080 ],
  animate: true
};

const params = {
  cols: 8,
  rows: 100,
  scaleMin: 0.5,
  scaleMax: 10,
  freq: 0.001,
  amp: 0.16,
  animate: true,
  frame: 0,
  lineCap: 'butt',
}

const sketch = () => {
  return ({ context, width, height, frame }) => {
    context.fillStyle = 'hsl(171, 42%, 8%)';
    context.fillRect(0, 0, width, height);

    const cols = params.cols;
    const rows = params.rows;
    const numCells = cols * rows;

    const gridw = width * 1;
    const gridh = height * 0.8;
    const cellw = gridw / cols;
    const cellh = gridh / rows;
    const margx = (width - gridw) * 0.5;
    const margy = (height - gridh) * 0.5;

    for (i = 0; i < numCells; i++) {
      const col = i % cols;
      const row = Math.floor(i / cols);

      const x = col * cellw + margx + cellw * 0.5;
      const y = row * cellh + margy + cellh * 0.5;
      const w = cellw * 0.8;
      const h = cellh * 0.8;


      // ternary operator
      const f = params.animate ? frame : params.frame;

      // const n = random.noise2D(x + frame * 10, y, params.freq);
      const n = random.noise3D(x, y, f * 10, params.freq);


      const angle = n * Math.PI * params.amp;
     
     //const scale = (n + 1) / 2 * 30;
     const scale = math.mapRange(n, -1, 1, params.scaleMin, params.scaleMax);

      context.save();
      context.translate(x, y);
      context.rotate(angle);

      context.lineWidth = scale;
      context.lineCap = params.lineCap;
      context.strokeStyle = 'hsl(171, 48%, 14%)';
      

      context.beginPath();
      context.moveTo(w * -0.5, 0);
      context.lineTo(w * 0.5, 0);
      context.stroke();

      context.restore();
    }
  };
};

const createPane = () => {
  const pane = new Tweakpane.Pane();
  let folder;

  folder = pane.addFolder({ title: 'Grid'});
  folder.addInput(params, 'lineCap', { options: { butt: 'butt', round: 'round', square: 'square' } });
  folder.addInput(params, 'cols', { min: 2, max: 100, step: 1});
  folder.addInput(params, 'rows', { min: 2, max: 100, step: 1});
  folder.addInput(params, 'scaleMin', { min: 0.5, max: 100});
  folder.addInput(params, 'scaleMax', { min: 1, max: 100});

  folder = pane.addFolder({ title: 'Noise'});
  folder.addInput(params, 'freq', { min: -0.01, max: 0.01});
  folder.addInput(params, 'amp', { min: 0, max: 1});
  folder.addInput(params, 'animate');
  folder.addInput(params, 'frame', { min: 0, max: 999 })
};

createPane();
canvasSketch(sketch, settings);
